<template>
  <div class="iframe_box">
    <vue-friendly-iframe :src="src" @load="onLoad"></vue-friendly-iframe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      src: this.$route.query.link
    };
  },
  mounted() {},
  methods: {
    onLoad() {}
  }
};
</script>
<style lang="scss" scoped>
.iframe_box {
  width: 100%;
  height: 100vh;
}
/deep/ .vue-friendly-iframe {
  height: 100vh;
}
/deep/ iframe {
  width: 100%;
  height: 100vh;
}
/*修改滚动条样式谷歌 start*/
/deep/ iframe html::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
/deep/ iframe html::-webkit-scrollbar-track {
  background: rgb(239, 239, 239);
  border-radius: 2px;
}
/deep/ iframe html::-webkit-scrollbar-thumb {
  background: #bfbfbf;
  border-radius: 6px;
}
/deep/ iframe html::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}
/deep/ iframe html::-webkit-scrollbar-corner {
  background: #179a16;
}
/*修改滚动条样式谷歌 end*/
/*修改滚动条样式火狐 start*/
/deep/ iframe html {
  scrollbar-color: rgb(239, 239, 239); /* 滑块颜色  滚动条背景颜色 */
  scrollbar-width: thin; /* 滚动条宽度有三种：thin、auto、none */
}
/*修改滚动条样式火狐 end*/
</style>



